<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <button @click="setStatus(Math.floor(Math.random()*4))">Change status</button>
    <p>{{status}}</p>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component';

@Options({
  props: {
    msg: String
  },
})
export default class HelloWorld extends Vue {
  msg!: string;
  stat = '';
  get status(): string{
    return this.$store.getters.statusDefine;
  }
  setStatus(value: number): void{
    console.log(value);
    this.$store.dispatch('SET_STATUS',value).then(()=>{
        this.stat = this.status;
    });
  }
}
</script>

